import React, { Component } from 'react'
import Button from '../Button'
import Modal from './Modal'

export const title = '按钮文字'

export const description = '第四个对话框'

export class Demo extends Component {
  state = {
    showModal: false,
  }
  render() {
    const { showModal } = this.state
    return (
      <>
        <Button data-testid="sizeModal" onClick={this.toggleModal}>
          显示可配置确认和取消按钮文字的对话框
        </Button>
        <Modal
          visible={showModal}
          title="默认对话框"
          onOk={this.toggleModal}
          onCancel={this.toggleModal}
          okText="同意"
          cancelText="放弃"
        >
          <div>对话框的内容</div>
        </Modal>
      </>
    )
  }

  toggleModal = () => {
    this.setState(state => ({ showModal: !state.showModal }))
  }
}

