import React, { Component } from 'react'
import Button from '../Button'
import Modal from './Modal'

export const title = '自定义页脚'

export const description = '第二个对话框'

export class Demo extends Component {
  state = {
    showModal: false,
  }
  render() {
    const { showModal } = this.state
    return (
      <>
        <Button data-testid="footerModal" onClick={this.toggleModal}>
          显示自定义页脚对话框
        </Button>
        <Modal
          visible={showModal}
          title="默认对话框"
          onOk={this.toggleModal}
          onCancel={this.toggleModal}
          footer={
            <Button onClick={this.toggleModal} data-testid="confirmModal">
              {'确认'}
            </Button>
          }
        >
          <div>对话框的内容</div>
        </Modal>
      </>
    )
  }

  toggleModal = () => {
    this.setState(state => ({ showModal: !state.showModal }))
  }
}

